import React, {
  useEffect,
  useState,
  useRef,
  useReducer,
  useMemo,
} from 'react';
import PropTypes from 'prop-types';

import './index.scss';
import * as utils from './utils';

import withSendbirdContext from '../../lib/SendbirdSdkContext';
import * as messageActionTypes from './dux/actionTypes';
import messagesInitialState from './dux/initialState';
import messagesReducer from './dux/reducers';
import eventHandler from './dux/eventListener';

import ConversationScroll from './components/ConversationScroll';
import Notification from './components/Notification';
import TypingIndicator from './components/TypingIndicator';
import ConnectionStatus from '../../ui/ConnectionStatus';
import ChatHeader from '../../ui/ChatHeader';
import MessageInput from '../../ui/MessageInput';
import { uuidv4 } from '../../utils/uuid';
import PlaceHolder from '../../ui/PlaceHolder';
import PlaceHolderTypes from '../../ui/PlaceHolder/type';
import { LabelStringSet } from '../../ui/Label';

const noop = () => { };

export const ConversationPanel = (props) => {
  const {
    channelUrl,
    stores: { sdkStore, userStore },
    config: {
      userId,
      logger,
      pubSub,
      isOnline,
    },
    dispatchers: {
      reconnect,
    },
    renderChatItem,
    onChatHeaderActionClick,
    onBeforeSendUserMessage,
    onBeforeSendFileMessage,
    onBeforeUpdateUserMessage,
  } = props;
  const { sdk } = sdkStore;
  const sdkError = sdkStore.error;
  const sdkInit = sdkStore.initialized;
  const { user } = userStore;

  const [messagesStore, messagesDispatcher] = useReducer(messagesReducer, messagesInitialState);
  const [messageSource, setMessageSource] = useState(null);
  const [invalidChannel, setInvalidChannel] = useState(false);
  const [channelHandlerId, setChannelHandlerId] = useState(uuidv4());
  // map to update read status of messages
  const [readStatus, setReadStatus] = useState({});
  const [messageReciverId, setMessageReciverId] = useState();
  const [currentGroupChannel, setCurrentGroupChannel] = useState({});
  const [emojiContainer, setEmojiContainer] = useState({});
  const messageInputRef = useRef(null);
  const scrollRef = useRef(null);

  // hack to rerender header when channel info changes
  // ChatHeader doesnt seem to recoganize changes in currentGroupChannel
  const [hash, setHash] = useState(uuidv4());

  // const { appInfo = {} } = sdk;
  const useReaction = false;

  const {
    allMessages,
    loading,
    hasMore,
    initialized,
    unreadCount,
    unreadSince,
  } = messagesStore;

  const emojiAllMap = useMemo(() => (
    useReaction
      ? utils.getAllEmojisMapFromEmojiContainer(emojiContainer)
      : new Map()
  ), [emojiContainer]);
  const emojiAllList = useMemo(() => (
    useReaction
      ? utils.getAllEmojisFromEmojiContainer(emojiContainer)
      : []
  ), [emojiContainer]);
  const nicknamesMap = useMemo(() => (
    useReaction
      ? utils.getNicknamesMapFromMembers(currentGroupChannel.members)
      : new Map()
  ), [currentGroupChannel.members]);

  // to create message-datasource
  useEffect(() => {
    logger.info('Channel: Setup started');
    messagesDispatcher({
      type: messageActionTypes.RESET_MESSAGES,
    });
    let prevMessageListQuery = null;

    if (!sdkInit) {
      logger.warning('Channel: SDK not ready');
      setMessageSource(null);
    }
    if (channelUrl && sdkInit) {
      logger.info('Channel: Set current channel', channelUrl);
      messagesDispatcher({
        type: messageActionTypes.SET_CURRENT_CHANNEL,
        payload: channelUrl,
      });
      // remove previous handler
      // to do - move to cleanup fn
      if (sdk && sdk.removeChannelHandler) {
        logger.info('Channel: Remove previous event handler');
        sdk.removeChannelHandler(channelHandlerId);
      }
      if (!sdk || !sdk.GroupChannel) { return; }
      sdk.GroupChannel.getChannel(channelUrl, (groupChannel) => {
        logger.info('Channel: Fetched channel', groupChannel);
        if (!groupChannel) {
          logger.warning('Channel: Invalid channel');
          setInvalidChannel(true);
          return;
        }

        setInvalidChannel(false);
        setCurrentGroupChannel(groupChannel);

        // for handling read status on the go
        const handler = new sdk.ChannelHandler();
        const handleMessageStatus = (c) => {
          if (channelUrl === c.url) {
            const allReadStatus = allMessages.reduce((accumulator, msg) => {
              if (msg.messageId !== 0) {
                return {
                  ...accumulator,
                  [msg.messageId]: utils.getParsedStatus(msg, groupChannel),
                };
              }
              return accumulator;
            }, {});
            setReadStatus(allReadStatus);
          }
        };
        handler.onDeliveryReceiptUpdated = handleMessageStatus;
        handler.onReadReceiptUpdated = handleMessageStatus;
        handler.onChannelChanged = () => {
          setHash(uuidv4());
        };

        handler.onChannelFrozen = (groupChannel_) => {
          setCurrentGroupChannel(groupChannel_);
        };
        handler.onChannelUnFrozen = (groupChannel_) => {
          setCurrentGroupChannel(groupChannel_);
        };

        const newHandlerId = uuidv4();
        sdk.addChannelHandler(newHandlerId, handler);
        setChannelHandlerId(newHandlerId);

        logger.info('Channel: Mark as read', groupChannel);
        // this order is important - this mark as read should update the event handler up above
        groupChannel.markAsRead();

        // There should only be one single instance per channel view.
        prevMessageListQuery = groupChannel.createPreviousMessageListQuery();
        prevMessageListQuery.limit = 30;
        prevMessageListQuery.reverse = false;
        prevMessageListQuery.includeReaction = true;
        // should set includeReaction true in below cases
        // https://sendbird.atlassian.net/wiki/spaces/SDK/pages/89981992/Message+reaction

        setMessageSource(prevMessageListQuery);

        logger.info('Channel: Fetching messages', groupChannel);
        messagesDispatcher({
          type: messageActionTypes.GET_PREV_MESSAGES_START,
        });

        // Retrieving previous messages.
        prevMessageListQuery.load((response, err) => {
          if (prevMessageListQuery.deprecatedRun) {
            logger.warning('Donot run prevMessageListQuery, because channel has been switched',
              prevMessageListQuery);
            return;
          }
          const swapParams = sdk.getErrorFirstCallback();
          let messages = response;
          let error = err;
          if (swapParams) {
            messages = error;
            error = response;
          }
          if (error) {
            logger.error('Channel: Fetching messages failed', error);
            return;
          }
          logger.info('Channel: Fetching messages success', messages);

          messagesDispatcher({
            type: messageActionTypes.GET_PREV_MESSAGES_SUCESS,
            payload: {
              messages,
              hasMore: prevMessageListQuery.hasMore,
            },
          });
          setTimeout(() => utils.scrollIntoLast('.sendbird-msg--scroll-ref'));
        });
      });
    }

    // eslint-disable-next-line consistent-return
    return () => {
      if (sdk && sdk.removeChannelHandler) {
        logger.info('Channel: Removing channel handler', channelHandlerId);
        sdk.removeChannelHandler(channelHandlerId);
      }

      if (prevMessageListQuery) {
        prevMessageListQuery.deprecatedRun = true;
      }
    };
  }, [channelUrl, sdkInit]);

  // todo: cleanup internal state - this is for removing messages from other person
  useEffect(() => {
    setMessageReciverId(uuidv4());
    if (channelUrl && sdk) {
      eventHandler({ messagesDispatcher, sdk, logger });
    }
    return () => {
      if (sdk && sdk.removeChannelHandler) {
        logger.info('Channel: Removing message reciver handler', messageReciverId);
        sdk.removeChannelHandler(messageReciverId);
      }
    };
  }, [channelUrl, sdkInit]);

  // handles API calls from withSendbird
  useEffect(() => {
    const subScriber = utils.pubSubHandler(channelUrl, pubSub, messagesDispatcher);
    return () => {
      utils.pubSubHandleRemover(subScriber);
    };
  }, [channelUrl, sdkInit]);

  useEffect(() => {
    if (!sdk || !sdk.getAllEmoji) {
      return;
    }
    sdk.getAllEmoji((emojiContainer_, err) => {
      if (err) {
        logger.error('Channel: Getting emojis failed', err);
        return;
      }
      logger.info('Channel: Getting emojis success', emojiContainer_);
      setEmojiContainer(emojiContainer_);
    });
  }, [channelUrl, sdkInit]);

  // to create initial read status
  useEffect(() => {
    if (allMessages.length > 0) {
      const allReadStatus = allMessages.reduce((accumulator, msg) => {
        if (msg.messageId !== 0) {
          return {
            ...accumulator,
            [msg.messageId]: utils.getParsedStatus(msg, currentGroupChannel),
          };
        }
        return accumulator;
      }, {});
      setReadStatus(allReadStatus);
    }
  }, [allMessages]);

  // handling connection breaks
  useEffect(() => {
    const wasOffline = !isOnline;
    return () => {
      let prevMessageListQuery = null;
      // state changed from offline to online
      if (wasOffline) {
        logger.info('Refreshing conversation state');
        if (!currentGroupChannel || !currentGroupChannel.createPreviousMessageListQuery) {
          return;
        }
        prevMessageListQuery = currentGroupChannel.createPreviousMessageListQuery();
        prevMessageListQuery.limit = 30;
        prevMessageListQuery.reverse = false;
        prevMessageListQuery.includeReaction = true;

        setMessageSource(prevMessageListQuery);

        // Retrieving previous messages.
        prevMessageListQuery.load((response, err) => {
          if (prevMessageListQuery.deprecatedRun) {
            logger.warning('Donot run prevMessageListQuery, because channel has been switched',
              prevMessageListQuery);
            return;
          }
          const swapParams = sdk.getErrorFirstCallback();
          let messages = response;
          let error = err;
          if (swapParams) {
            messages = error;
            error = response;
          }
          if (error) {
            logger.error('Channel: Fetching messages failed', error);
            return;
          }
          logger.info('Channel: Fetching messages success', messages);

          messagesDispatcher({
            type: messageActionTypes.CLEAR_SENT_MESSAGES,
          });

          messagesDispatcher({
            type: messageActionTypes.GET_PREV_MESSAGES_SUCESS,
            payload: {
              messages,
              hasMore: prevMessageListQuery.hasMore,
            },
          });
          setTimeout(() => utils.scrollIntoLast('.sendbird-msg--scroll-ref'));
        });
      }
    };
  }, [isOnline]);

  if (sdkError) {
    return (
      <div className="sendbird-conversation">
        <PlaceHolder
          type={PlaceHolderTypes.WRONG}
          retryToConnect={() => {
            logger.info('Channel: reconnecting');
            reconnect();
          }}
        />
      </div>
    );
  }
  if (!channelUrl) {
    return (<div className="sendbird-conversation"><PlaceHolder type={PlaceHolderTypes.NO_CHANNELS} /></div>);
  }
  if (loading) {
    return (<div className="sendbird-conversation"><PlaceHolder type={PlaceHolderTypes.LOADING} /></div>);
  }
  if (invalidChannel) {
    return (<div className="sendbird-conversation"><PlaceHolder type={PlaceHolderTypes.WRONG} /></div>);
  }

  return (
    <div className="sendbird-conversation">
      <ChatHeader
        hash={hash}
        currentGroupChannel={currentGroupChannel}
        currentUser={user}
        onActionClick={onChatHeaderActionClick}
        subTitle={currentGroupChannel.members && currentGroupChannel.members.length !== 2}
        isActive={false}
        isMuted={false}
      />
      {
        unreadCount > 0 && (
          <Notification
            count={unreadCount}
            onClick={() => {
              utils.scrollIntoLast('.sendbird-msg--scroll-ref');
              // there is no scroll
              if (scrollRef.current.scrollTop === 0) {
                currentGroupChannel.markAsRead();
                messagesDispatcher({
                  type: messageActionTypes.MARK_AS_READ,
                });
              }
            }}
            time={unreadSince}
          />
        )
      }
      <ConversationScroll
        swapParams={
          sdk && sdk.getErrorFirstCallback && sdk.getErrorFirstCallback()
        }
        scrollRef={scrollRef}
        hasMore={hasMore}
        disabled={!isOnline}
        messagesDispatcher={messagesDispatcher}
        messageSource={messageSource}
        initialized={initialized}
        allMessages={allMessages}
        userId={userId}
        editDisabled={utils.isDisabledBecauseFrozen(currentGroupChannel)}
        readStatus={readStatus}
        currentGroupChannel={currentGroupChannel}
        renderChatItem={renderChatItem}
        useReaction={useReaction}
        emojiContainer={emojiContainer}
        emojiAllList={emojiAllList}
        emojiAllMap={emojiAllMap}
        membersMap={nicknamesMap}
        deleteMessage={(message, cb) => {
          logger.info('Channel: Deleting message', message);
          const { requestState } = message;
          logger.info('Channel: Deleting message requestState:', requestState);

          // Message is only on local
          if (requestState === 'failed') {
            logger.info('Channel: Deleted message from local:', message);
            messagesDispatcher({
              type: messageActionTypes.ON_MESSAGE_DELETED_BY_REQ_ID,
              payload: message.reqId,
            });
            if (cb) { cb(); }
            return;
          }

          // Message is on server
          currentGroupChannel.deleteMessage(message, (err) => {
            logger.info('Channel: Deleting message from remote:', requestState);
            if (cb) { cb(err); }
            if (!err) {
              logger.info('Channel: Deleting message success!', message);
              messagesDispatcher({
                type: messageActionTypes.ON_MESSAGE_DELETED,
                payload: message.messageId,
              });
            } else {
              logger.warning('Channel: Deleting message failed!', err);
            }
          });
        }}
        updateMessage={(messageId, text, cb) => {
          const createParamsDefault = (txt) => {
            const params = new sdk.UserMessageParams();
            params.message = txt;
            return params;
          };

          const createCustomPrams = onBeforeUpdateUserMessage
            && typeof onBeforeUpdateUserMessage === 'function';

          if (createCustomPrams) {
            logger.info('Channel: creting params using onBeforeUpdateUserMessage', onBeforeUpdateUserMessage);
          }

          const params = onBeforeUpdateUserMessage
            ? onBeforeUpdateUserMessage(text)
            : createParamsDefault(text);

          currentGroupChannel.updateUserMessage(messageId, params, (r, e) => {
            logger.info('Channel: Updating message!', params);
            const swapParams = sdk.getErrorFirstCallback();
            let message = r;
            let err = e;
            if (swapParams) {
              message = e;
              err = r;
            }
            if (cb) {
              cb(err, message);
            }
            if (!err) {
              logger.info('Channel: Updating message success!', message);
              messagesDispatcher({
                type: messageActionTypes.ON_MESSAGE_UPDATED,
                payload: message,
              });
            } else {
              logger.warning('Channel: Updating message failed!', err);
            }
          });
        }}
        resendMessage={(failedMessage) => {
          logger.info('Channel: Resending message has started', failedMessage);
          const { messageType, file } = failedMessage;
          if (failedMessage && typeof failedMessage.isResendable === 'function'
            && failedMessage.isResendable()
          ) {
            // eslint-disable-next-line no-param-reassign
            failedMessage.requestState = 'pending';
            messagesDispatcher({
              type: messageActionTypes.RESEND_MESSAGEGE_START,
              payload: failedMessage,
            });

            // userMessage
            if (messageType === 'user') {
              currentGroupChannel
                .resendUserMessage(failedMessage)
                .then((message) => {
                  logger.info('Channel: Resending message success!', { message });
                  messagesDispatcher({
                    type: messageActionTypes.SEND_MESSAGEGE_SUCESS,
                    payload: message,
                  });
                })
                .catch((e) => {
                  logger.warning('Channel: Resending message failed!', { e });
                  // eslint-disable-next-line no-param-reassign
                  failedMessage.requestState = 'failed';
                  messagesDispatcher({
                    type: messageActionTypes.SEND_MESSAGEGE_FAILURE,
                    payload: failedMessage,
                  });
                });

              // eslint-disable-next-line no-param-reassign
              failedMessage.requestState = 'pending';
              messagesDispatcher({
                type: messageActionTypes.RESEND_MESSAGEGE_START,
                payload: failedMessage,
              });
              return;
            }

            if (messageType === 'file') {
              currentGroupChannel
                .resendFileMessage(failedMessage, file)
                .then((message) => {
                  logger.info('Channel: Resending file message success!', { message });
                  messagesDispatcher({
                    type: messageActionTypes.SEND_MESSAGEGE_SUCESS,
                    payload: message,
                  });
                })
                .catch((e) => {
                  logger.warning('Channel: Resending file message failed!', { e });
                  // eslint-disable-next-line no-param-reassign
                  failedMessage.requestState = 'failed';
                  messagesDispatcher({
                    type: messageActionTypes.SEND_MESSAGEGE_FAILURE,
                    payload: failedMessage,
                  });
                });

              // eslint-disable-next-line no-param-reassign
              failedMessage.requestState = 'pending';
              messagesDispatcher({
                type: messageActionTypes.RESEND_MESSAGEGE_START,
                payload: failedMessage,
              });
            }
          } else {
            // to alert user on console
            // eslint-disable-next-line no-console
            console.error('Message is not resendable');
            logger.warning('Message is not resendable', failedMessage);
          }
        }}
        toggleReaction={(message, key, isReacted) => {
          if (isReacted) {
            currentGroupChannel.deleteReaction(message, key)
              .then((res) => {
                logger.info('Delete reaction success', res);
              })
              .catch((err) => {
                logger.warning('Delete reaction failed', err);
              });
            return;
          }
          currentGroupChannel.addReaction(message, key)
            .then((res) => {
              logger.info('Add reaction success', res);
            })
            .catch((err) => {
              logger.warning('Add reaction failed', err);
            });
        }}
      />
      <div className="sendbird-conversation__footer">
        <MessageInput
          placeholder={utils.isDisabledBecauseFrozen(currentGroupChannel)
            && LabelStringSet.CHANNEL__MESSAGE_INPUT__PLACE_HOLDER__DISABLED}
          ref={messageInputRef}
          disabled={!initialized || utils.isDisabledBecauseFrozen(currentGroupChannel) || !isOnline}
          onStartTyping={() => {
            currentGroupChannel.startTyping();
          }}
          onSendMessage={() => {
            const text = messageInputRef.current.value;
            const createParamsDefault = (txt) => {
              const params = new sdk.UserMessageParams();
              params.message = txt;
              return params;
            };

            const createCustomPrams = onBeforeSendUserMessage
              && typeof onBeforeSendUserMessage === 'function';

            if (createCustomPrams) {
              logger.info('Channel: creting params using onBeforeSendUserMessage', onBeforeSendUserMessage);
            }

            const params = onBeforeSendUserMessage
              ? onBeforeSendUserMessage(text)
              : createParamsDefault(text);

            logger.info('Channel: Sending message has started', params);
            const pendingMsg = currentGroupChannel.sendUserMessage(params, (res, err) => {
              const swapParams = sdk.getErrorFirstCallback();
              let message = res;
              let error = err;
              if (swapParams) {
                message = err;
                error = res;
              }
              // sending params instead of pending message
              // to make sure that we can resend the message once it fails
              if (error) {
                logger.warning('Channel: Sending message failed!', { message });
                messagesDispatcher({
                  type: messageActionTypes.SEND_MESSAGEGE_FAILURE,
                  payload: message,
                });
                return;
              }
              logger.info('Channel: Sending message success!', message);
              messagesDispatcher({
                type: messageActionTypes.SEND_MESSAGEGE_SUCESS,
                payload: message,
              });
            });
            messagesDispatcher({
              type: messageActionTypes.SEND_MESSAGEGE_START,
              payload: pendingMsg,
            });
            setTimeout(() => utils.scrollIntoLast('.sendbird-msg--scroll-ref'));
          }}
          onFileUpload={(file) => {
            const createParamsDefault = (file_) => {
              const params = new sdk.FileMessageParams();
              params.file = file_;
              return params;
            };

            const createCustomPrams = onBeforeSendFileMessage
              && typeof onBeforeSendFileMessage === 'function';

            if (createCustomPrams) {
              logger.info('Channel: creting params using onBeforeSendFileMessage', onBeforeSendFileMessage);
            }

            const params = onBeforeSendFileMessage
              ? onBeforeSendFileMessage(file)
              : createParamsDefault(file);

            logger.info('Channel: Uploading file message start!', params);
            const pendingMsg = currentGroupChannel.sendFileMessage(params, (response, err) => {
              const swapParams = sdk.getErrorFirstCallback();
              let message = response;
              let error = err;
              if (swapParams) {
                message = err;
                error = response;
              }
              if (error) {
                // sending params instead of pending message
                // to make sure that we can resend the message once it fails
                logger.error('Channel: Sending file message failed!', message);
                message.url = URL.createObjectURL(file);
                message.file = file;
                messagesDispatcher({
                  type: messageActionTypes.SEND_MESSAGEGE_FAILURE,
                  payload: message,
                });
                return;
              }
              logger.info('Channel: Sending message success!', message);
              messagesDispatcher({
                type: messageActionTypes.SEND_MESSAGEGE_SUCESS,
                payload: message,
              });
            });
            messagesDispatcher({
              type: messageActionTypes.SEND_MESSAGEGE_START,
              payload: {
                ...pendingMsg,
                url: URL.createObjectURL(file),
                // pending thumbnail message seems to be failed
                requestState: 'pending',
              },
            });
            setTimeout(() => utils.scrollIntoLast('.sendbird-msg--scroll-ref'), 1000);
          }}
        />
        <div className="sendbird-conversation__typing-indicator">
          <TypingIndicator channelUrl={channelUrl} sb={sdk} logger={logger} />
        </div>
        {
          !isOnline && (
            <ConnectionStatus sdkInit={sdkInit} sb={sdk} logger={logger} />
          )
        }
      </div>
    </div>
  );
};

ConversationPanel.propTypes = {
  channelUrl: PropTypes.string,
  stores: PropTypes.shape({
    sdkStore: PropTypes.shape({
      initialized: PropTypes.bool,
      sdk: PropTypes.shape({
        getErrorFirstCallback: PropTypes.func,
        removeChannelHandler: PropTypes.func,
        GroupChannel: PropTypes.any,
        ChannelHandler: PropTypes.any,
        addChannelHandler: PropTypes.func,
        UserMessageParams: PropTypes.any,
        FileMessageParams: PropTypes.any,
        getAllEmoji: PropTypes.func,
        appInfo: PropTypes.shape({}),
      }),
      error: PropTypes.bool,
    }),
    userStore: PropTypes.shape({
      user: PropTypes.shape({}),
    }),
  }).isRequired,
  dispatchers: PropTypes.shape({
    reconnect: PropTypes.func,
  }).isRequired,
  config: PropTypes.shape({
    userId: PropTypes.string.isRequired,
    isOnline: PropTypes.bool.isRequired,
    logger: PropTypes.shape({
      info: PropTypes.func,
      error: PropTypes.func,
      warning: PropTypes.func,
    }),
    pubSub: PropTypes.shape({
      subscribe: PropTypes.func,
      publish: PropTypes.func,
    }),
  }).isRequired,
  onBeforeSendUserMessage: PropTypes.func, // onBeforeSendUserMessage(text)
  onBeforeSendFileMessage: PropTypes.func, // onBeforeSendFileMessage(File)
  onBeforeUpdateUserMessage: PropTypes.func,
  renderChatItem: PropTypes.element,
  onChatHeaderActionClick: PropTypes.func,
};

ConversationPanel.defaultProps = {
  channelUrl: null,
  onBeforeSendUserMessage: null,
  onBeforeSendFileMessage: null,
  onBeforeUpdateUserMessage: null,
  renderChatItem: null,
  onChatHeaderActionClick: noop,
};

export const {
  getEmojiCategoriesFromEmojiContainer,
  getAllEmojisFromEmojiContainer,
  getEmojisFromEmojiContainer,
} = utils;

export default withSendbirdContext(ConversationPanel);
