import * as messageActions from './actionTypes';

export default ({
  messagesDispatcher,
  sdk,
  uniqueId,
  logger,
}) => {
  if (!sdk || !sdk.ChannelHandler) { return; }
  const ChannelHandler = new sdk.ChannelHandler();
  logger.info('Channel: Setup event handler');
  ChannelHandler.onMessageReceived = (channel, message) => {
    logger.info('Channel: onMessageReceived', message);
    messagesDispatcher({
      type: messageActions.ON_MESSAGE_RECEIVED,
      payload: { channel, message },
    });
  };

  ChannelHandler.onMessageUpdated = (_, message) => {
    logger.info('Channel: onMessageUpdated', message);
    messagesDispatcher({
      type: messageActions.ON_MESSAGE_UPDATED,
      payload: message,
    });
  };

  ChannelHandler.onMessageDeleted = (_, messageId) => {
    logger.info('Channel: onMessageDeleted', messageId);
    messagesDispatcher({
      type: messageActions.ON_MESSAGE_DELETED,
      payload: messageId,
    });
  };

  ChannelHandler.onReactionUpdated = (_, reactionEvent) => {
    logger.info('Channel: onReactionUpdated', reactionEvent);
    messagesDispatcher({
      type: messageActions.ON_REACTION_UPDATED,
      payload: reactionEvent,
    });
  };

  // Add this channel event handler to the SendBird object.
  sdk.addChannelHandler(uniqueId, ChannelHandler);
};
